.contactsBlock {
    min-height: 50vh;
    background-color: #cb8265;
    display: flex;


}

.container {
    border: 1px solid;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;



}
.title{
    background-color: #a1d3d3;
}

.contacts {
    width: 100%;
    border: 1px solid;
    /*background-color: #7a8383;*/
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}